<template>
  <div>
   <div class="title">热销推荐</div>
    <ul>
      <router-link tag='li' class="item border-bottom" v-for="item of list" :key="item.id" :to="'/detail/' + item.id" >
        <img class="item-img" :src="item.imgUrl" />
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
          <button class="item-button">查看详情</button>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeRecommend',
  props: {
    list: Array
  }
}
</script>
<style lang="stylus" scoped>
  @import '~style/mixins.styl'
  .title
    margin-top: 10px
    font-size: 14px
    text-indent: 10px
    line-height: 40px
    background: #eee
  .item
    overflow: hidden
    height: 94px
    display: flex
    .item-img
      width: 95px
      height: 85px
      padding: 5px
    .item-info
      flex: 1
      padding: 5px
      min-width: 0
      .item-title
        line-height: 27px
        font-size: 16px
        color: #000
        ellipsis()
      .item-desc
        line-height: 20px
        color: #ccc
        ellipsis()
      .item-button
        line-height: 22px
        margin-top: 8px
        border-radius: 3px
        padding: 0 10px
        color: #fff
        background-color: #ef7300
</style>



