<template>
    <div class="header">
      <div class="header-left"><div class="iconfont back-icon">&#xe624;</div></div>
      <div class="header-input"><span class="iconfont search-icon">&#xe632;</span>请输入城市/景点/游玩主题</div>
      <router-link to="/city">
        <div class="header-right">{{this.city}}<span class="iconfont arrow-icon">&#xe64a;</span></div>
      </router-link>
    </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'HomeHeader',
  computed: {
    ...mapState(['city'])
  }
}
</script>
<style lang="stylus" scoped>
  @import "~style/varibles.styl";
  .header
    height: 43px
    line-height: $headerHeight
    display: flex
    background-color: $bgColor
    color: #fff
    .header-left
      width: 32px
      float: left
      .back-icon
        font-size: 20px
        text-align: center
    .header-input
      flex: 1
      height: 32px
      line-height: 32px
      margin: 6px 0 0 10px
      background-color: #fff
      color: #ccc
      border-radius: 5px
      padding-left: 10px
      .search-icon
        margin-right: 5px
    .header-right
      min-width: 54px
      padding: 0 5px
      float: right
      text-align: center
      color: #fff
      .arrow-icon
        font-size: 12px
        margin-left: 4px
</style>



