<template>
  <div class="icons">
    <swiper :options="swiperOption">
      <swiper-slide v-for="(page, index) of pages" :key="index">
        <div class="icon" v-for="item of page" :key="item.id">
          <div class="icon-img">
            <img class="icon-img-content" :src="item.imgUrl" />
          </div>
          <p class="icon-desc">{{item.desc}}</p>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeIcon',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        autoplay: false,
        pagination: '.swiper-pagination'
      }
    }
  },
  computed: {
    pages () {
      const pages = []
      this.list.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>
<style lang="stylus" scoped>
  @import "~style/varibles.styl";
  @import "~style/mixins.styl";
  .icons >>> .swiper-container
    height: 0
    overflow: hidden
    padding-bottom: 50%

  .icons
    margin-top: 5px
    .icon
      position: relative
      overflow: hidden
      width: 25%
      height: 0
      padding-bottom: 25%
      float: left
      .icon-img
        position: absolute
        top: 0
        left: 0
        right: 0
        bottom: 22px
        box-sizing: border-box
        padding: 5px
        .icon-img-content
          display: block
          margin: 0 auto
          height: 100%
      .icon-desc
          text-align: center
          line-height: 22px
          position: absolute
          left: 0
          right: 0
          bottom: 0
          color: $darkTextColor
          ellipsis()
</style>



