<template>
  <div>
    <div class="banner" @click="handleBannerClick">
      <img
        class="banner-img"
        :src=this.bannerImg
      />
      <div class="banner-info">
        <div class="banner-title">{{this.name}}</div>
        <div class="banner-number"><span class="iconfont banner-icon">&#xe692;</span>{{this.galleryImg.length}}</div>
      </div>
    </div>
    <fade-animation>
      <common-gallery :imgs="galleryImg" v-show="showGallery" @close="handleGalleryClose"></common-gallery>
    </fade-animation>
  </div>
</template>

<script>
import CommonGallery from 'common/gallery/Gallery'
import FadeAnimation from 'common/fade/FadeAnimation'
export default {
  name: 'DetailBanner',
  props: {
    name: String,
    bannerImg: String,
    galleryImg: Array
  },
  components: {
    CommonGallery,
    FadeAnimation
  },
  data () {
    return {
      showGallery: false
    }
  },
  methods: {
    handleBannerClick () {
      this.showGallery = true
    },
    handleGalleryClose () {
      this.showGallery = false
    }
  }
}
</script>
<style lang="stylus" scoped>
  .banner
    position: relative
    overflow: hidden
    height: 0
    padding-bottom: 55%
    .banner-img
      width: 100%
    .banner-info
      display: flex
      position: absolute
      left: 0
      right: 0
      bottom: 0
      line-height: 30px
      color: #fff
      background-image: linear-gradient(top, rgba(0, 0 , 0, 0), rgba(0 ,0 ,0, 0.8))
      .banner-title
        flex: 1
        font-size: 16px
        padding: 0 10px
      .banner-number
        padding: 0 20px
        margin-top: 7px
        line-height: 16px
        height: 16px
        font-size: 12px
        border-radius: 10px
        background: rgba(0, 0, 0, 0.8)
        .banner-icon
          font-size: 12px
          display: inline-block
          margin-right: 5px
</style>



