<template>
  <div>
    <div class="title">周末去哪儿</div>
    <ul>
      <li class="item border-bottom" v-for="item of list" :key="item.id">
        <div class="img-wrapper"><img class="item-img" :src="item.imgUrl" /></div>
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeWeekend',
  props: {
    list: Array
  }
}
</script>
<style lang="stylus" scoped>
  @import '~style/mixins.styl'
  .title
    font-size: 14px
    text-indent: 10px
    line-height: 40px
    background: #eee
  .img-wrapper
    overflow: hidden
    height: 0
    padding-bottom: 37.09%
    .item-img
      width: 100%
  .item-info
    padding: 5px
    .item-title
      line-height: 27px
      font-size: 16px
      color: #000
      ellipsis()
    .item-desc
      line-height: 20px
      color: #ccc
      ellipsis()
</style>



