<template>
  <div>
      <router-link tag='div' class="header-abs" to="/" v-show="showAbs">
        <div class="iconfont abs-back">&#xe624;</div>
      </router-link>
    <div class="header-fixed" v-show="!showAbs" :style="opacityStyle">
      <router-link tag='div' to="/">
        <div class="iconfont fixed-back">&#xe624;</div>
      </router-link>
      景点详情
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailHeader',
  data () {
    return {
      showAbs: true,
      opacityStyle: {
        opacity: 0
      }
    }
  },
  methods: {
    handleScroll () {
      console.log('scroll')
      const top = document.documentElement.scrollTop || document.body.scrollTop
      if (top > 60) {
        this.showAbs = false
        let opacity = top / 140
        opacity = opacity > 1 ? 1 : opacity
        this.opacityStyle = { opacity }
      } else {
        this.showAbs = true
      }
    }
  },
  activated () {
    window.addEventListener('scroll', this.handleScroll)
  },
  deactivated () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>
<style lang="stylus" scoped>
  @import '~style/varibles.styl'
  .header-abs
    position: absolute
    top: 10px
    left: 10px
    width: 40px
    height: 40px
    line-height: 40px
    text-align: center
    border-radius: 20px
    background: rgba(0, 0, 0 , 0.8)
    .abs-back
      color: #fff
      font-size: 20px
  .header-fixed
    z-index: 2
    position: fixed
    top: 0
    left: 0
    right: 0
    height: $headerHeight
    line-height: $headerHeight
    text-align: center
    background: $bgColor
    color: #fff
    font-size: 16px
    overflow: hidden
    .fixed-back
      position: absolute
      left: 0
      top: 0
      width: 32px
      text-align: center
      font-size: 20px
      color: #fff
</style>



