<template>
  <div>
    <detail-banner :name="sightName" :bannerImg="bannerImg" :galleryImg="galleryImgs"></detail-banner>
    <detail-header></detail-header>
    <div class="test">
      <detail-list :list="categoryList"></detail-list>
    </div>
  </div>
</template>

<script>
import DetailBanner from './components/Banner'
import DetailHeader from './components/Header'
import DetailList from './components/List'
import axios from 'axios'
export default {
  name: 'Detail',
  components: {
    DetailBanner,
    DetailHeader,
    DetailList
  },
  data () {
    return {
      sightName: '',
      bannerImg: '',
      galleryImgs: [],
      categoryList: [],
      lastSightId: ''
    }
  },
  methods: {
    getDetailInfo () {
      axios.get('http://yapi.demo.qunar.com/mock/72425/detail', {
        params: {
          id: this.$route.params.id
        }
      }).then(this.handleDetailData)
    },
    handleDetailData (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.sightName = data.sightName
        this.bannerImg = data.bannerImg
        this.galleryImgs = data.galleryImgs
        this.categoryList = data.categoryList
      }
    }
  },
  mounted () {
    this.lastSightId = this.$route.params.id
    this.getDetailInfo()
  },
  activated () {
    if (this.lastSightId !== this.$route.params.id) {
      this.lastSightId = this.$route.params.id
      this.getDetailInfo()
    }
  }
}
</script>
<style lang="stylus" scoped>
  .test
    height: 20rem
</style>



