<template>
  <div>
    <div class="search">
      <input class="search-input" type="text"
             placeholder="请输入城市名或拼音" v-model="keyword"/>
    </div>
    <div class="search-content" v-show="keyword" ref="search">
      <ul>
        <li
          class="search-item border-bottom" v-for="item of list" :key="item.id"
          @click="handleCityClick(item.name)"
        >{{item.name}}</li>
        <li class="search-item border-bottom" v-if="hasNoData">没有找到匹配数据</li>
      </ul>
    </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
import { mapMutations } from 'vuex'
export default {
  name: 'CitySearch',
  props: {
    cities: Object
  },
  data () {
    return {
      keyword: '',
      timer: null,
      list: []
    }
  },
  methods: {
    handleCityClick (city) {
      this.changeCity(city)
      this.$router.push('/')
    },
    ...mapMutations(['changeCity'])
  },
  computed: {
    hasNoData () {
      return !this.list.length
    }
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.search, { mouseWheel: true, click: true, tap: true })
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      this.timer = setTimeout(() => {
        const result = []
        for (let i in this.cities) {
          this.cities[i].forEach((value) => {
            if (value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1) {
              result.push(value)
            }
          })
        }
        this.list = result
      }, 100)
    }
  }
}
</script>
<style lang="stylus" scoped>
  @import '~style/varibles.styl'
  .search
    height: 36px
    padding: 0 5px
    background: $bgColor
    .search-input
      box-sizing: border-box
      padding: 0 5px
      width: 100%
      height: 31px
      line-height: 31px
      text-align: center
      border-radius: 3px
      color: #666
  .search-content
    overflow: hidden
    position: absolute
    z-index: 1
    top: 79px
    left: 0
    bottom: 0
    background: #eee
    width: 100%
    .search-item
      line-height: 31px
      padding-left: 10px
      background: #fff
      color: #666
</style>



